ul {
  list-style: none;
  margin: auto;
  padding: 0;
}

.editPage button {
  margin-bottom: 10px;
}

.editListTopBar {
  max-width: 500px;
  margin: auto;
  border-top: 1px solid black;
  position: relative;
  height: 28px;
}

.editListTopBarLeft {
  left: 0px;
  position: absolute;
}

.editListTopBarMiddle {
  width: 80%;
  margin: auto;
  float: left;
}

.editListTopBarMiddle p {
  width: 50%;
  margin: 0;
  float: left;
}

.editListTopBarRight {
  right: 0px;
  position: absolute;
}

/* Popup settings */

.empty {
  display: none;
}

.editPopupBackground {
  position: fixed;
  left: 0;
  z-index: 1;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
}

.editPopup {
  background-color: #fefefe;
  position: absolute;
  max-width: 500px;
  width: 100%;
  height: 40px;
  border: 1px solid black;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.editPopupCancel {
  float: left;
  margin-right: 4px;
}

.editPopupCancel:hover {
  cursor: pointer;
}

.editPopupInputs {
  width: 90%;
  margin: 5px auto;
}

.editPopupInputs input {
  float: left;
  width: 45%;
  margin: 1px;
  border-width: 2px;
}

.editPopupDone {
  right: 0;
  position: absolute;
}

.editPopupDone:hover {
  cursor: pointer;
}

.inputErrors {
  float: none;
  width: 90%;
  background-color: aqua;
  margin: auto;
}

.inputErrors div {
  width: 45%;
  margin: 0 3px 0 5px;
  background-color: white;
  text-align: left;
  float: left;
}

.hideErrorEnglish {
  visibility: hidden;
}

.showErrorEnglish {
  visibility: visible;
}

.hideErrorFinnish {
  visibility: hidden;
}

.showErrorFinnish {
  visibility: visible;
}
